import React, { useState } from 'react';
import Hero from '../components/shared/hero';
import Layout from "../components/layout"
import { graphql } from 'gatsby';
import SEO from "../components/seo";
import styled from 'styled-components';
import ScrollAnimation from 'react-animate-on-scroll';

import '../styles/business.css';
import Reasons from '../components/shared/reasons';
import constants from '../constants/business';
import BottomCta from '../components/shared/bottom-cta';
import TestimonialSlider from '../components/shared/testimonial-slider';
import CallCTA from '../components/business/call-cta';
import VerticalSteps from '../components/shared/vertical-steps';
import CollapsibleSection from '../components/shared/collapsable';
import ClientLogosSection from '../components/business/client-logos-section';

import leftImageSteps from '../images/business/vertical-steps.svg'
import meta from "../constants/meta-descriptions";
import FormPopup from '../components/shared/form-popup';

const BusinessPage = ({ data }) => {
  const [showForm, setShowForm] = useState(false);

  return (
    <>
      <div className='business-page'>
        <BackgroundWrapper
          bg={data.bgImage.childImageSharp.fluid.src}
        >
          <Layout
            activeTab={'/za-biznesa/'}
          >
            <SEO
              title={meta.business.title}
              description={meta.business.metaDescription}
            />

            <Hero
              heroImage={data.heroImage.childImageSharp.fluid.src}
              imageClass='hero-business'
              title={(
                <>
                  <h1 className='d-none d-sm-inline-block text-white mt-5 mx-1' style={{ whiteSpace: 'pre-line' }}>     ,<br />    </h1>
                  <h1 className='d-inline d-sm-none text-white mt-5 mx-2' style={{ whiteSpace: 'pre-line' }}>   <br /> ,<br />    </h1>
                </>
              )}
            />

            <ScrollAnimation
              animateIn='fadeIn'
              animateOnce={true}
            >
              <ClientLogosSection
                logos={data.logos}
              />
            </ScrollAnimation>

            <ScrollAnimation
              animateIn='fadeIn'
              animateOnce={true}
            >
              <Reasons
                sectionTitle={<h2 className='text-white' style={{ whiteSpace: 'pre-wrap' }}>  <br />      <br /> NetSurf</h2>}
                reasonsData={constants.reasons}
                textClassName={'text-white'}
                isDark={true}
              />
            </ScrollAnimation>

            <ScrollAnimation
              animateIn='fadeIn'
              animateOnce={true}
            >

              <CallCTA
                bgImg={data.ctaBg.childImageSharp.fluid.src}
                bgPerson={data.ctaPerson.childImageSharp.fluid}
              >
                <div className='text-md-left text-center'>
                  <h2 className='mb-md-5 mb-2 d-nones d-md-blocks text-white'>  ,<br />   </h2>
                  <p className='mb-md-5 mb-3 text-white'>         .</p>
                  <div className='d-inline-block'>
                    <a href={'tel:088 299 1611'} className='btn btn-primary green text-left d-inline-block mt-2 mr-md-2 mb-2'>   088 299 1611</a>
                  </div>
                  <div className='d-inline-block'>
                    <button className='btn btn-primary dark-blue' onClick={(_) => setShowForm(true)}> </button>
                  </div>
                </div>
              </CallCTA>
            </ScrollAnimation>

            <ScrollAnimation
              animateIn='fadeIn'
              animateOnce={true}
            >
              <VerticalSteps
                title={<h2 className='text-white'>  <br />   <br /> </h2>}
                isDark={true}
                steps={constants.verticalSteps}
                leftImage={leftImageSteps}
              />
            </ScrollAnimation>

            <ScrollAnimation
              animateIn='fadeIn'
              animateOnce={true}
            >
              <TestimonialSlider
                sectionTitle={<h2 className='text-white'> <br />  </h2>}
                opinions={constants.clientOpinions}
                isWhiteArrows={true}
                isDark={true}
              />
            </ScrollAnimation>

            <ScrollAnimation
              animateIn='fadeIn'
              animateOnce={true}
            >
              <BottomCta
                bgImg={data.ctaBg.childImageSharp.fluid.src}
              >
                <h2 className='mb-4 d-none d-md-block text-white'>    <br />   !</h2>
                <h2 className='mb-4 d-block d-md-none text-white'>       !</h2>
                <p className='mb-4 text-white'>      </p>
                <button className='btn btn-primary green' onClick={(_) => setShowForm(true)}>  </button>
              </BottomCta>
            </ScrollAnimation>

            <ScrollAnimation
              animateIn='fadeIn'
              animateOnce={true}
            >
              <CollapsibleSection
                isDark={true}
                faqs={constants.faqs}
                sectionTitle={(
                  <>
                    <h2 className='text-white' style={{ whiteSpace: 'pre-wrap' }}>  </h2>
                    <hr className='center blue' />
                  </>
                )}
              />
            </ScrollAnimation>
          </Layout>
        </BackgroundWrapper>
      </div>

      <FormPopup
        show={showForm}
        setShow={setShowForm}
        isBusiness={true}
      />
    </>
  )
}

const BackgroundWrapper = styled.div`
  background-image: url(${props => props.bg});
      background-position: top;
      background-size: cover;
      background-repeat: repeat-y;
`

export const query = graphql`
  query BusinessPageQuery {
    heroImage: file(relativePath: {eq: "business/hero-header.png"}) {
      childImageSharp {
        fluid(maxHeight: 1852) {
          src
        }
      }
    }
    bgImage: file(relativePath: {eq: "business/business-service-bg.png"}) {
      childImageSharp {
        fluid(maxHeight: 6056) {
          src
        }
      }
    }
    ctaBg: file(relativePath: {eq: "business/cta-bg.png"}) {
      childImageSharp {
        fluid(maxHeight: 838) {
          src
        }
      }
    }
    ctaPerson: file(relativePath: {eq: "business/call-cta-person.png"}) {
      childImageSharp {
        fluid(maxHeight: 1038) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    logos: allFile(filter: {sourceInstanceName: {eq: "logo-clients-images"}}) {
      edges {
        node {
          childImageSharp {
            fluid(maxWidth: 360) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }`


export default BusinessPage;
